<template>
  <div id="app">
    <!-- 搭建框架 -->
    <section class="decorate-wrapper">
      <div class="decorate-content" :class="{'drag-active':dragActive}">
        <div class="decorate-header"></div>
        <!-- 根据页面数据渲染组件列表 -->
        <draggable v-model="pagelist" @start="_dragStart" @end="_dragEnd"
                   :options="{animation: 300,handle:'.design-handle',forceFallback: true,fallbackTolerance:1}">
          <decorateitem v-for="(item,index) in pagelist" :parmes="item" :editshow="editcomponentshow===item.id"
                        :editactive="editcomponentactive===item.id" :key="item.id"
                        @removeComponent="removeComponent(index)" @showModal="showModal(index,item.id,$event)"
                        @showEdit="showEdit(item.id)"></decorateitem>
        </draggable>
        <!-- 页面底部添加组件模块 -->
        <div class="component-box">
          <choosecomponents @addComponent="addComponent('push',$event)"></choosecomponents>
        </div>
        <!-- 组件内部点击添加弹出的添加组件模块 -->
        <div class="choose-component-fixed" v-show="choosecomponentshow" ref="choosefixed">
          <choosecomponents @addComponent="addComponent('add',$event)"></choosecomponents>
        </div>
      </div>
    </section>
    <!-- 搭建框架 end -->
  </div>
</template>

<script>
  // 拖拽元素
  import draggable from 'vuedraggable'
  // 组件元素
  import decorateitem from 'decorate/decorateitem/decorateitem'
  // 添加组件模块
  import choosecomponents from 'decorate/choosecomponents/choosecomponents'

  // 定义随机id长度
  const idLength = 5

  export default {
    name: 'app',
    data() {
      return {
        choosecomponentshow: false, // 悬浮添加组件模块是否显示，true or false
        chooseIndex: 0, // 悬浮添加组件索引值
        chooseType: '', // 悬浮添加组件类型（向上还是向下添加）
        editcomponentactive: false, // 是否选中组件，false or 组件id
        editcomponentshow: false, // 编辑组件模块是否显示，false or 组件id
        dragActive: false, // 是否正在拖拽，true or false
        // 页面搭建的组件列表
        pagelist: [
          {
            id: '000001',
            type: 'vheader',
            name: '页面标题',
            data: {}
          },
          {
            id: '000002',
            type: 'swiper',
            name: '切换广告',
            data: {
              piclist: [
                {
                  src: '',
                  title: '',
                  link: ''
                }
              ]
            }
          },
          {
            id: '000003',
            type: 'navigation',
            name: '图文导航',
            data: {}
          },
          {
            id: '000004',
            type: 'vtitle',
            name: '标题',
            data: {
              title: '标题',
              position: 'center',
              backgroundColor: '#fff'
            }
          },
          {
            id: '000005',
            type: 'productlist',
            name: '商品',
            data: {}
          }
        ]
      }
    },
    methods: {
      // 页面添加组件
      addComponent(type, data) {
        // 对象深拷贝，避免互相影响
        console.log(type, data)
        let parmes = JSON.parse(JSON.stringify(data))
        let id = this._getId(idLength)
        parmes.id = id
        if (type === 'push') {
          // 页面底部添加组件
          this.pagelist.push(parmes)
        } else {
          // 页面中间部分添加组件
          let index = this.chooseIndex
          if (this.chooseType === 'bottom') {
            index++
          }
          this.pagelist.splice(index, 0, parmes)
        }
        // 选中并编辑新添加的组件
        this.showEdit(id)
      },
      // 移除某个组件
      removeComponent(index) {
        this.pagelist.splice(index, 1)
      },
      // 显示添加组件的悬浮框
      showModal(index, id, type) {
        this.editcomponentactive = id
        this.editcomponentshow = false
        this.choosecomponentshow = true
        this.chooseIndex = index
        this.chooseType = type
        let rect = event.target.getBoundingClientRect()
        let srcollTop = document.documentElement.scrollTop || document.body.scrollTop
        this.$refs.choosefixed.style.top = (rect.top + srcollTop) + 'px'
      },
      // 显示编辑组件的悬浮框
      showEdit(id) {
        this.choosecomponentshow = false
        this.editcomponentactive = id
        this.editcomponentshow = id
      },
      // 开始拖拽
      _dragStart() {
        this.dragActive = true
        this.choosecomponentshow = false
        console.log('开始')
      },
      // 结束拖拽
      _dragEnd(evt) {
        this.dragActive = false
        console.log(evt)
      },
      // 生成组件随机ID
      _getId(length) {
        return Number(Math.random().toString().substr(3, length) + Date.now()).toString(36)
      }
    },
    components: {
      draggable,
      decorateitem,
      choosecomponents
    }
  }
</script>

<style>
  /*装修页面样式*/
  #app {
    background: #fff;
  }

  .decorate-wrapper {
    width: 815px;
    margin: 0 auto;
    padding: 20px 0;
  }

  .decorate-content {
    width: 372px;
    min-height: 500px;
    border: 1px solid #ddd;
    background: #f6f6f6;
  }

  .decorate-content.drag-active .design-handle:before, .decorate-content.drag-active .decorate-dragger .design-add, .decorate-content.drag-active .decorate-dragger .design-close {
    display: none;
  }

  .decorate-content .decorate-header {
    width: 100%;
    height: 28px;
    background: url(header_bg.png) left top no-repeat;
    background-size: 100% 100%;
  }

  .component-box {
    border-top: 1px solid #ddd;
    background: #fff;
  }

  .choose-component-fixed {
    position: absolute;
    left: 50%;
    margin: -20px 0 0 -15px;
    width: 320px;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 4px;
  }

  .choose-component-fixed:before, .choose-component-fixed:after {
    content: '';
    position: absolute;
    left: -12px;
    top: 20px;
    width: 0;
    height: 0;
    border-width: 6px;
    border-style: solid;
    border-color: transparent #ddd transparent transparent;
  }

  .choose-component-fixed:after {
    left: -11px;
    border-color: transparent #fff transparent transparent;
  }
</style>



