<template>
  <!-- 首页滚动广告  -->
  <section class="store-swiper">
    <swiper :options="swiperOption">
      <swiper-slide>
        <img src="./banner_1.jpg">
      </swiper-slide>
      <swiper-slide>
        <img src="./banner_2.jpg">
      </swiper-slide>
      <swiper-slide>
        <img src="./banner_3.jpg">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </section>
  <!-- 首页滚动广告 end  -->
</template>

<script type="text/ecmascript-6">
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'

  export default {
    name: 'vswiper',
    data() {
      return {
        swiperOption: {
          notNextTick: true,
          autoplay: true,
          pagination: {
            el: '.swiper-pagination'
          }
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>

<style scoped>
  .store-swiper {
    width: 100%;
    height: 7.7rem;
    text-align: center
  }
  .store-swiper img {
    width: 100%;
    height: 7.7rem;
  }
  .swiper-pagination-bullet-active {
    background: #fe5478;
  }
</style>



