<template>
  <div>
    <ul class="up-pic-list">
      <li v-for="(item,index) in parmes.piclist" :key="index">
        <div class="pic"><img :src="item.src"></div>
        <div class="info">
          <el-form :model="item" label-width="50px" class="demo-ruleForm">
            <el-form-item label="标题">
              <el-input v-model="item.title"></el-input>
            </el-form-item>
            <el-form-item label="链接">
              <el-input v-model="item.link"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </li>
    </ul>
    <div class="add-pic">
      <p><i class="el-icon-plus"></i> 添加一个广告图</p>
      建议尺寸 750x350 像素
    </div>
    <p class="add-info">最多添加10个广告，拖动图片广告可对其排序</p>
  </div>
</template>

<script>
  export default {
    name: 'swiper',
    props: ['parmes']
  }
</script>

<style scoped>
  .up-pic-list li {
    display: flex;
    padding: 15px;
    margin-bottom: 15px;
    background: #ffffff;
    border: 1px dashed #e5e5e5;
  }
  .up-pic-list li .pic {
    width: 80px;
    height: 80px;
    margin-right: 10px;
  }
  .up-pic-list li .pic img {
    width: 80px;
    height: 80px;
  }
  .up-pic-list li .info {
    flex: 1;
  }
  .add-pic {
    height: 80px;
    cursor: pointer;
    background: #fff;
    border: 1px dashed #e5e5e5;
    text-align: center;
  }

  .add-pic p {
    margin-top: 22px;
    color: #3366cc;
  }

  .add-info {
    padding-top: 20px;
    color: #999;
  }
</style>



