<template>
  <div>
    <h2 class="index-title" :class="{'left':parmes.position==='left'}"
        :style="{backgroundColor: parmes.backgroundColor}">
      <span>{{parmes.title}}</span>
    </h2>
  </div>
</template>

<script>
  export default {
    name: 'vtitle',
    props: ['parmes']
  }
</script>

<style scoped>
  .index-title {
    text-align: center;
    background: #fff;
    padding: .2rem 0 .2rem;
  }

  .index-title span {
    display: inline-block;
    vertical-align: top;
    font-size: .6rem;
    font-weight: bold;
    padding: .3rem .1rem;
    border-bottom: 2px solid #fe5478;
  }

  .index-title.left {
    text-align: left;
  }

  .index-title.left span {
    padding-left: 10px;
    border-bottom: none;
    border-left: 2px solid #fe5478;
  }
</style>



