<template>
  <!-- 单个组件模块 -->
  <div class="decorate-item">
    <div class="decorate-dragger">
      <div class="design-handle" @click="showEdit" :class="{'active':editactive}">
        <!-- 组件内容，根据父级传入组件类型渲染组件内容 -->
        <component v-bind:is="currentComponent" :parmes="parmes.data"></component>
      </div>
      <el-popover
        placement="left"
        width="160"
        v-model="visible">
        <p class="popover-content">确认删除该组件？</p>
        <div style="text-align: right; margin: 0">
          <span class="vbtn vbtn-blue-line" @click.stop="visible = false">取消</span>
          <span class="vbtn vbtn-blue" @click.stop="removeComponent">确定</span>
        </div>
        <div class="design-close" slot="reference">
          <i class="el-icon-error"></i>
        </div>
      </el-popover>
      <div class="design-add add-top" @click.stop="showModal('top')">
        <span class="add-btn">
          <i class="el-icon-plus"></i>
        </span>
      </div>
      <div class="design-add add-bottom" @click.stop="showModal('bottom')">
        <span class="add-btn">
          <i class="el-icon-plus"></i>
        </span>
      </div>
    </div>
    <div class="component-edit" v-if="editshow">
      <h3 class="edit-title">{{parmes.name}}</h3>
      <!-- 组件编辑内容，根据父级传入组件类型渲染组件编辑内容 -->
      <component v-bind:is="currentComponentConfig" :parmes="parmes.data"></component>
    </div>
  </div><!-- 单个组件模块 end -->
</template>

<script>
  // 页面所有可展示组件
  import vheader from 'components/vheader/vheader'
  import swiper from 'components/swiper/swiper'
  import navigation from 'components/navigation/navigation'
  import vtitle from 'components/vtitle/vtitle'
  import productlist from 'components/productlist/productlist'

  // 页面组件对应的配置组件
  import vheaderConfig from 'componentsconfig/vheader/vheader'
  import swiperConfig from 'componentsconfig/swiper/swiper'
  import navigationConfig from 'componentsconfig/navigation/navigation'
  import vtitleConfig from 'componentsconfig/vtitle/vtitle'
  import productlistConfig from 'componentsconfig/productlist/productlist'

  export default {
    name: 'decorateitem',
    props: ['parmes', 'editactive', 'editshow'],
    data() {
      return {
        visible: false
      }
    },
    computed: {
      // 父级传入组件类型与实际组件对应关系
      currentComponent() {
        return this.parmes.type
      },
      currentComponentConfig() {
        let type = this.parmes.type
        let component = type + 'Config'
        return component
      }
    },
    methods: {
      // 移除某个组件
      removeComponent() {
        this.visible = false
        this.$emit('removeComponent')
      },
      // 显示添加组件模态框
      showModal(type) {
        this.$emit('showModal', type)
      },
      // 选中组件进行编辑
      showEdit() {
        this.$emit('showEdit')
      }
    },
    components: {
      // 页面组件
      vheader,
      swiper,
      navigation,
      vtitle,
      productlist,

      // 页面组件对应的编辑组件
      vheaderConfig,
      swiperConfig,
      navigationConfig,
      vtitleConfig,
      productlistConfig
    }
  }
</script>

<style>
  .decorate-item {
    position: relative;
    cursor: move;
  }

  .decorate-item.sortable-ghost {
    opacity: .2;
  }

  .decorate-dragger, .design-handle {
    position: relative;
  }

  .decorate-dragger .design-close {
    position: absolute;
    right: -10px;
    top: -10px;
    cursor: pointer;
    z-index: 52;
    display: none;
  }

  .decorate-dragger .design-close i {
    display: block;
    width: 20px;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
    background: #fff;
    color: #999;
    border-radius: 50%;
  }

  .decorate-dragger .design-close:hover i {
    color: #666;
  }

  .decorate-dragger .design-add {
    position: absolute;
    left: 0;
    height: 0;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #36c;
    z-index: 51;
    display: none;
  }

  .decorate-dragger .design-add:before, .decorate-dragger .design-add:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    margin-top: -3px;
    width: 4px;
    height: 4px;
    background: #fff;
    border: 1px solid #36c;
    border-radius: 50%;
  }

  .decorate-dragger .design-add:after {
    left: auto;
    right: 0;
  }

  .decorate-dragger .design-add .add-btn {
    position: absolute;
    left: 50%;
    top: 0;
    margin-left: -9px;
    margin-top: -9px;
    width: 18px;
    height: 18px;
    background: #36c;
    color: #fff;
    border-radius: 50%;
    text-align: center;
    font-size: 12px;
    cursor: pointer;
  }

  .decorate-dragger .design-add.add-top {
    top: 0;
  }

  .decorate-dragger .design-add.add-bottom {
    bottom: 0;
  }

  .design-handle:hover:before, .design-handle.active:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border: 1px dashed #3366cc;
    z-index: 50;
    cursor: grab;
  }

  .decorate-dragger:hover .design-close, .decorate-dragger:hover .design-add {
    display: block;
  }

  .el-popover {
    position: absolute;
    background: #fff;
    min-width: 150px;
    border-radius: 4px;
    border: 1px solid #ebeef5;
    padding: 12px;
    z-index: 2000;
    color: #606266;
    line-height: 1.4;
    text-align: justify;
    font-size: 14px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  }

  .el-popper[x-placement^=left] {
    margin-right: 10px;
  }

  .el-popper .popper__arrow {
    border-width: 6px;
    filter: drop-shadow(0 2px 12px rgba(0, 0, 0, .03));
  }

  .el-popper .popper__arrow, .el-popper .popper__arrow:after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
  }

  .el-popper .popper__arrow:after {
    content: " ";
    border-width: 6px;
  }

  .el-popper[x-placement^=left] .popper__arrow {
    top: 50%;
    right: -6px;
    margin-bottom: 3px;
    border-left-color: #ebeef5;
    border-right-width: 0;
  }

  .el-popper[x-placement^=left] .popper__arrow:after {
    bottom: -6px;
    right: 1px;
    border-left-color: #fff;
    border-right-width: 0;
  }

  .popover-content {
    margin-bottom: 10px;
  }

  .vbtn {
    display: inline-block;
    margin-left: 5px;
    padding: 0 5px;
    font-size: 12px;
    line-height: 24px;
    border-radius: 2px;
    border: 1px solid transparent;
    cursor: pointer;
  }

  .vbtn-blue {
    border-color: #3366CC;
    background: #3366CC;
    color: #ffffff;
  }

  .vbtn-blue-line {
    border-color: #dcdfe6;
    background: #fff;
    color: #222;
  }

  /*组件参数配置*/
  .component-edit {
    position: absolute;
    left: 100%;
    top: 0;
    margin-left: 20px;
    padding: 12px 10px;
    min-height: 28px;
    background: #f8f8f8;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 420px;
    box-sizing: border-box;
    cursor: default;
  }

  .component-edit:before, .component-edit:after {
    content: '';
    position: absolute;
    left: -12px;
    top: 20px;
    width: 0;
    height: 0;
    border-width: 6px;
    border-style: solid;
    border-color: transparent #ddd transparent transparent;
  }

  .component-edit:after {
    left: -11px;
    border-color: transparent #f8f8f8 transparent transparent;
  }

  .component-edit .edit-title {
    margin-bottom: 15px;
    padding-bottom: 5px;
    border-bottom: 1px solid #e5e5e5;
  }

  .component-edit .el-form-item__label, .component-edit .el-form-item__content {
    font-size: 12px;
    line-height: 30px;
  }

  .component-edit .el-input {
    width: 200px;
  }

  .component-edit .el-input__inner {
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
    border-radius: 2px;
  }
  .component-edit  .el-form-item:last-child {
    margin-bottom: 0;
  }
</style>



