<template>
  <div>
    <el-form :model="parmes" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题名" prop="title">
        <el-input v-model="parmes.title"></el-input>
      </el-form-item>
      <el-form-item label="显示位置">
        <el-radio-group v-model="parmes.position">
          <el-radio label="left">居左显示</el-radio>
          <el-radio label="center">居中显示</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="背景颜色">
        <el-color-picker v-model="parmes.backgroundColor"></el-color-picker>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'vtitle',
    props: ['parmes'],
    data() {
      return {
        rules: {
          title: [
            {required: true, message: '标题不能为空', trigger: 'blur'}
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



